import { Component, OnInit, OnDestroy, AfterViewInit, ChangeDetectorRef, ViewChild, Directive, ElementRef, Renderer2 } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, NgForm } from '@angular/forms';
import { Router } from '@angular/router';
import { DataService } from 'src/app/_services/data.services';
import { CountryISO, SearchCountryField } from "ngx-intl-tel-input";
declare var $: any;
import Swal from 'sweetalert2';
import * as moment from 'moment';
import { TranslationService } from 'src/app/translation.service';

@Component({
  selector: 'app-Profile-cmp',
  templateUrl: './Profile.component.html'
})

export class ProfileComponent implements OnInit, AfterViewInit, OnDestroy {
  campaignOne: FormGroup;
  campaignTwo: FormGroup;
  step = 0;
  pstep = 1;
  subsstep = 0;
  passtext = "password";
  ishidden = true;
  passtext1 = "password";
  ishidden1 = true;
  passtext11 = "password";
  ishidden11 = true;
  passtext111 = "password";
  ishidden111 = true;
  passtext2 = "password";
  ishidden2 = true;
  user = null;
  data = null;
  showAlert = false;
  showPasswordAlert = false;
  showEmailAlert = false;
  showSuccessAlert = false;
  showCheckPasswordAlert = false;
  showOTPAlert = false;
  showEmailSuccessAlert = false;
  successMessage = ''
  errorMessage = ''
  new_email = null;
  languages = null;
  forgot_password_email = null;
  showFeedbackErrorAlert = false;
  showFeedbackSuccessAlert = false;
  user_id = null;
  subscriptions = null;
  moment: any = moment;
  plans = null;
  plan_id = null;
  level = null;
  paypalInterval = null;
  showSubscribeAlert = false;
  paymentDetails = null;

  @ViewChild('userNgForm') userNgForm: NgForm;
  userForm: FormGroup;
  @ViewChild('verifyEmailNgForm') verifyEmailNgForm: NgForm;
  verifyEmailForm: FormGroup;
  @ViewChild('verifyForgotPasswordOtpNgForm') verifyForgotPasswordOtpNgForm: NgForm;
  verifyForgotPasswordOtpForm: FormGroup;
  @ViewChild('updatePasswordNgForm') updatePasswordNgForm: NgForm;
  updatePasswordForm: FormGroup;
  @Directive({ selector: '[preventCutCopyPaste]' })
  @ViewChild('planNgForm') planNgForm: NgForm;
  planForm: FormGroup;

  separateDialCode = false;
  SearchCountryField = SearchCountryField;
  CountryISO = CountryISO;
  preferredCountries: CountryISO[] = [
    CountryISO.UnitedStates,
    CountryISO.UnitedKingdom
  ];
  currentLang='en';

  constructor( private translationService: TranslationService,private router: Router,
    private cdf: ChangeDetectorRef,
    private _formBuilder: FormBuilder,
    private dataService: DataService,
    renderer: Renderer2,
    el: ElementRef,) {
    var events = 'cut copy paste';
    events.split(' ').forEach(e =>
      renderer.listen(el.nativeElement, e, (event) => {
        event.preventDefault();
      })
    );
  }

  ngOnInit() {
    if (localStorage.getItem("token") != null) {
      this.user = 'exists';
    }
    else {
      //this.user = null;
     // localStorage.clear();
     // this.router.navigate(['/guest/home'])
    }
    const body = document.getElementsByTagName('body')[0];
    body.classList.add('lock-page');
    body.classList.add('off-canvas-sidebar');
    const card = document.getElementsByClassName('card')[0];

    this.getUser()
    // this.getLanguages()

    this.verifyEmailForm = this._formBuilder.group({
      email: null
    });

    this.verifyForgotPasswordOtpForm = this._formBuilder.group({
      email: null,
      otp: null
    });

    this.updatePasswordForm = this._formBuilder.group({
      email: null,
      password: null,
      confirm_password: null
    })

    this.planForm = this._formBuilder.group({
      plan_id: null,
      type: 'card',
      number: null,
      month: null,
      year: null,
      cvc: null
    })

    this.getSubscription()
    this.getPlans()
    this.getPaymentDetails()
    this.showPaymentSection()
  }
  onLangChange(currentLang: string) {
    this.translationService.useLang(currentLang);
  }
  languages1 = [
    { name: 'English', lang: 'en' },
    { name: 'french', lang: 'fr' },
    { name: 'German', lang: 'gr' },
    { name: 'Italian', lang: 'it' },
    { name: 'Spanisch', lang: 'sp' },
  ]
  ngOnDestroy() {
    const body = document.getElementsByTagName('body')[0];
    body.classList.remove('lock-page');
    body.classList.remove('off-canvas-sidebar');
  }

  ngAfterViewInit() {

  }

  getUser() {
    this.dataService.get('host/get-user-data').then((result) => {
      this.data = result.data;
      this.user_id = this.data.id
      this.userForm = this._formBuilder.group({
        fname: this.data.fname,
        lname: this.data.lname,
        email: this.data.email,
        contact_no: this.data.country_calling_code + this.data.contact_no,
        address: this.data.address,
        website: this.data.website,
        vat_no: this.data.vat_no,
        pic: this.data.pic
      });
      this.userForm.controls['email'].disable();
    })
  }

  getLanguages() {
    this.dataService.get('host/get-all-languages').then((result) => {
      this.languages = result.data
      this.dataService.get('guest/get-user-languages').then((result) => {
        let langList = result.data;
        if (langList != null) {
          for (let i = 0; i < langList.length; i++) {
            if (langList[i]['lang_id'] == this.languages[i]['id']) {
              $('#lang' + (i + 1)).prop('checked', true)
            }
          }
        }
        this.cdf.detectChanges()
      })
    })
  }

  getSubscription() {
    this.dataService.get('get-subscribed').then((result) => {
      this.subscriptions = result.data;
      this.cdf.detectChanges();
    })
  }

  getPlans() {
    this.dataService.get('host/get_plan').then((result) => {
      this.plans = result.data
      this.cdf.detectChanges()
    })
  }

  getPaymentDetails() {
    this.dataService.get('host/get-payment-details').then((result) => {
      this.paymentDetails = result.data
      this.cdf.detectChanges()
    })
  }
  
  subscrioption() {
    $('#payments').removeClass("btn-danger");
    $('#subs').removeClass("btn-default");
    $('#subs').addClass("btn-danger");
    $('#payments').addClass("btn-default");
    this.subsstep = 1;
  }

  payments() {
    $('#subs').removeClass("btn-danger");
    $('#payments').addClass("btn-danger");
    $('#payments').removeClass("btn-default");
    $('#subs').addClass("btn-default");
    this.subsstep = 0;
    this.subsstep = 0;
    this.pstep = 1;
    this.cdf.detectChanges();
  }

  show() {
    this.ishidden = !this.ishidden
    if (this.ishidden == true) {
      this.passtext = "password";
      $('#pshides').html("Show");
    }
    else {
      this.passtext = "text";
      $('#pshides').html("Hide");
    }
    this.cdf.detectChanges();
  }

  show1() {
    this.ishidden1 = !this.ishidden1;
    if (this.ishidden1 == true) {
      this.passtext1 = "password";
      $('#pshide1').html("Show");
    }
    else {
      this.passtext1 = "text";
      $('#pshide1').html("Hide");
    }
    this.cdf.detectChanges();
  }

  show11() {
    this.ishidden11 = !this.ishidden11;
    if (this.ishidden11 == true) {
      this.passtext11 = "password";
      $('#pshide11').html("Show");
    }
    else {
      this.passtext11 = "text";
      $('#pshide11').html("Hide");
    }
    this.cdf.detectChanges();
  }

  show111() {
    this.ishidden111 = !this.ishidden111;
    if (this.ishidden111 == true) {
      this.passtext111 = "password";
      $('#pshide111').html("Show");
    }
    else {
      this.passtext111 = "text";
      $('#pshide111').html("Hide");
    }
    this.cdf.detectChanges()
  }

  show2() {
    this.ishidden2 = !this.ishidden2;
    if (this.ishidden2 == true) {
      this.passtext2 = "password";
      $('#pshide2').html("Show");
    }
    else {
      this.passtext2 = "text";
      $('#pshide2').html("Hide");

    }
  }

  clickProfilePicture() {
    $('#pictureFileTrigger').click();
  }

  editProfilePicture(event: any) {
    const file: File = event.target.files[0];
    if (file) {
      const fd = new FormData();

      fd.append('fname', this.data.fname)
      fd.append('lname', this.data.lname)
      fd.append('contact_no', this.data.contact_no)
      fd.append('address', this.data.address)
      fd.append('website', this.data.website)
      fd.append('vat_no', this.data.vat_no)
      fd.append('pic', file);

      this.dataService.post('guest/update-personal-info', fd).then((result) => {
        this.data.pic = result.data.pic
        this.cdf.detectChanges();
      },
        error => {
          Swal.fire({
            position: 'center',
            icon: 'error',
            text: 'Soemthing went wrong. Please try again.',
            showConfirmButton: false,
            timer: 2500
          })
          this.cdf.detectChanges();
        }
      )
    }
  }

  update() {
    this.showAlert = false;

    if (this.userForm.invalid) {
      const invalid = [];
      const controls = this.userForm.controls;
      for (const name in controls) {
        if (controls[name].invalid) {
          invalid.push(name);
        }
      }
      if (invalid[0] == 'fname') {
        Swal.fire({
          position: 'center',
          icon: 'error',
          text: 'Please Provide First Name With Minimum 4 Chars At Least',
          showConfirmButton: false,
          timer: 2500
        })
      }
      else if (invalid[0] == 'lname') {
        Swal.fire({
          position: 'center',
          icon: 'error',
          text: 'Please Provide Last Name With Minimum 4 Chars At Least',
          showConfirmButton: false,
          timer: 2500
        })
      }
      else if (invalid[0] == 'contact_no') {
        Swal.fire({
          position: 'center',
          icon: 'error',
          text: 'Please Provide Valid Phone Number. You may see placeholder for phone number format.',
          showConfirmButton: false,
          timer: 2500
        })
      }
      else if (invalid[0] == 'website') {
        Swal.fire({
          position: 'center',
          icon: 'error',
          text: 'Please Provide Valid URL. You may see placeholder for website format.',
          showConfirmButton: false,
          timer: 2500
        })
      }
      this.cdf.detectChanges()
      return
    }

    const formData = this.userForm.getRawValue();

    const data = {
      fname: formData.fname,
      lname: formData.lname,
      contact_no: formData.contact_no.nationalNumber,
      country_calling_code: formData.contact_no.dialCode,
      country_code: formData.contact_no.countryCode,
      address: formData.address,
      website: formData.website,
      vat_no: formData.vat_no,
      pic: null
    };

    this.dataService.post('guest/update-personal-info', data).then((result) => {
      this.getUser();
      this.step = 0;
      this.cdf.detectChanges();
    },
      error => {
        Swal.fire({
          position: 'center',
          icon: 'error',
          text: 'Something went wrong. Please try again.',
          showConfirmButton: false,
          timer: 2500
        })
        this.cdf.detectChanges();
      }
    )
  }

  updatePassword() {
    this.showPasswordAlert = false
    this.showSuccessAlert = false
    let current_password = $('#current_password').val();
    let new_password = $('#new_password').val();
    let confirm_password = $('#confirm_password').val();

    if (!current_password || !new_password || !confirm_password) {
      Swal.fire({
        position: 'center',
        icon: 'error',
        text: 'Please Fill All Required Fields',
        showConfirmButton: false,
        timer: 2500
      })
      this.cdf.detectChanges()
    }
    else {
      if (new_password != confirm_password) {
        Swal.fire({
          position: 'center',
          icon: 'error',
          text: 'Password does not match.',
          showConfirmButton: false,
          timer: 2500
        })
        this.cdf.detectChanges()
      }
      else {

        const data = {
          old_password: current_password,
          new_password: new_password
        };

        this.dataService.post('guest/change-password', data).then((result) => {
          if (result.success == true) {
            $('#current_password').val(null);
            $('#new_password').val(null);
            $('#confirm_password').val(null);
            Swal.fire({
              position: 'center',
              icon: 'success',
              text: 'Password Updated Successfully',
              showConfirmButton: false,
              timer: 2500
            })
          }
          else {
            Swal.fire({
              position: 'center',
              icon: 'error',
              text: result.error.message,
              showConfirmButton: false,
              timer: 2500
            })
          }
          this.cdf.detectChanges();
        },
          error => {
            Swal.fire({
              position: 'center',
              icon: 'error',
              text: 'Something went wrong. Please try again.',
              showConfirmButton: false,
              timer: 2500
            })
            this.cdf.detectChanges();
          }
        )
      }
    }

  }

  updatePasswordCancel() {
    $('#current_password').val(null);
    $('#new_password').val(null);
    $('#confirm_password').val(null);
    this.cdf.detectChanges();
  }

  checkPassword() {
    this.showCheckPasswordAlert = false
    let validate_password = $('#validate_password').val()
    if (!validate_password) {
      Swal.fire({
        position: 'center',
        icon: 'error',
        text: 'Please Fill All Required Fields',
        showConfirmButton: false,
        timer: 2500
      })
      this.cdf.detectChanges()
    }
    else {
      const data = {
        password: validate_password
      };

      this.dataService.post('guest/check-password', data).then((result) => {
        if (result.success == true) {
          $('#new_email').prop('disabled', false);
          $('#confirm_email').prop('disabled', false);
          $('#new_email').focus();
          $('#validate_password').val(null);
        }
        else {
          Swal.fire({
            position: 'center',
            icon: 'error',
            text: 'Incorrect Password',
            showConfirmButton: false,
            timer: 2500
          })
        }
        this.cdf.detectChanges();
      },
        error => {
          Swal.fire({
            position: 'center',
            icon: 'error',
            text: 'Something went wrong. Please try again.',
            showConfirmButton: false,
            timer: 2500
          })
          this.cdf.detectChanges();
        }
      )
    }
  }

  checkPasswordCancel() {
    $('#validate_password').val(null);
    this.cdf.detectChanges()
  }

  updateEmail() {
    this.showEmailAlert = false;
    this.new_email = $('#new_email').val();
    let confirm_email = $('#confirm_email').val();

    if (this.new_email != confirm_email) {
      Swal.fire({
        position: 'center',
        icon: 'error',
        text: 'Email does not match',
        showConfirmButton: false,
        timer: 2500
      })
    }
    else {
      const data = {
        email: this.new_email
      };

      this.dataService.post('guest/change-email', data).then((result) => {
        if (result.success == true) {
          $('#new_email').val(null);
          $('#confirm_email').val(null);
          $('#confirm_email').prop('disabled', true);
          $('#new_email').prop('disabled', true);
          $('#otpSection').prop('hidden', false);
          $('#otp').focus();
        }
        else {
          Swal.fire({
            position: 'center',
            icon: 'error',
            text: result.error.message,
            showConfirmButton: false,
            timer: 2500
          })
        }
        // this.cdf.detectChanges();
      },
        error => {
          Swal.fire({
            position: 'center',
            icon: 'error',
            text: 'Soemthing went wrong. Please try again.',
            showConfirmButton: false,
            timer: 2500
          })
          this.cdf.detectChanges();
        }
      )
    }
  }

  updateEmailCancel() {
    $('#new_email').val(null);
    $('#confirm_email').val(null);
    $('#new_email').prop('disabled', true);
    $('#confirm_email').prop('disabled', true);
  }

  updateOTP() {
    this.showOTPAlert = false;
    this.showEmailSuccessAlert = false;
    let otp = $('#otp').val()
    if (!otp) {
      Swal.fire({
        position: 'center',
        icon: 'error',
        text: 'Please Provide OTP Code',
        showConfirmButton: false,
        timer: 2500
      })
    }
    else {
      const data = {
        email: this.new_email,
        otp: otp
      };

      this.dataService.post('guest/verify-otp', data).then((result) => {
        if (result.success == true) {
          $('#otpSection').prop('hidden', true);
          Swal.fire({
            position: 'center',
            icon: 'success',
            text: 'Email Updated Successfully',
            showConfirmButton: false,
            timer: 2500
          })
        }
        else {
          Swal.fire({
            position: 'center',
            icon: 'error',
            text: 'Email already exists.',
            showConfirmButton: false,
            timer: 2500
          })
        }
        this.cdf.detectChanges();
      },
        error => {
          Swal.fire({
            position: 'center',
            icon: 'error',
            text: 'Soemthing went wrong. Please try again.',
            timer: 2500
          })
          this.cdf.detectChanges();
        }
      )
    }
  }

  updateOTPCancel() {
    $('#otp').val(null);
    $('#otpSection').prop('hidden', true);
  }

  updateLanguages() {
    let language_id = []
    $('.languages').each(function () {
      if ($(this).is(":checked")) {
        language_id.push($(this).val());
      }
    })

    const data = {
      language_id: language_id
    }

    this.dataService.post('guest/update-languages', data).then((result) => {
      this.getLanguages()
    })
  }

  verifyEmail() {
    this.errorMessage = null;
    this.showAlert = false;
    if (this.verifyEmailForm.invalid) {
      Swal.fire({
        position: 'center',
        icon: 'error',
        text: 'Please Fill All Required Fields',
        showConfirmButton: false,
        timer: 2500
      })
      this.cdf.detectChanges()
      return
    }

    const formData = this.verifyEmailForm.getRawValue();
    this.forgot_password_email = formData.email;
    const data = {
      email: this.forgot_password_email
    }
    this.dataService.post('verify-email', data).then((result) => {
      if (result.success == true) {
        $('#vEmail').prop('hidden', true);
        $('#vForgotPasswordOtp').prop('hidden', false);
        formData.email = null;
      }
      else {
        Swal.fire({
          position: 'center',
          icon: 'error',
          text: 'Something went wrong. Please try again.',
          showConfirmButton: false,
          timer: 2500
        })
      }
      this.cdf.detectChanges()
    })
  }

  verifyForgoPasswordOtp() {
    this.errorMessage = null;
    this.showAlert = false;
    if (this.verifyForgotPasswordOtpForm.invalid) {
      Swal.fire({
        position: 'center',
        icon: 'error',
        text: 'Please Fill All Required Fields',
        showConfirmButton: false,
        timer: 2500
      })
      this.cdf.detectChanges()
      return
    }

    const formData = this.verifyForgotPasswordOtpForm.getRawValue();
    const data = {
      email: this.forgot_password_email,
      otp: formData.otp
    }
    this.dataService.post('verify-forgot-password-otp', data).then((result) => {
      if (result.success == true) {
        $('#vForgotPasswordOtp').prop('hidden', true);
        $('#uPassword').prop('hidden', false);
        formData.otp = null;
      }
      else {
        Swal.fire({
          position: 'center',
          icon: 'error',
          text: 'Something went wrong. Please try again.',
          showConfirmButton: false,
          timer: 2500
        })
      }
      this.cdf.detectChanges()
    })
  }

  updatePasswordOtp() {
    this.errorMessage = null;
    this.showAlert = false;
    if (this.updatePasswordForm.invalid) {
      Swal.fire({
        position: 'center',
        icon: 'error',
        text: 'Please Fill All Required Fields',
        showConfirmButton: false,
        timer: 2500
      })
      this.cdf.detectChanges()
      return
    }

    const formData = this.updatePasswordForm.getRawValue();
    if (formData.password != formData.confirm_password) {
      Swal.fire({
        position: 'center',
        icon: 'error',
        text: 'Password does not match.',
        showConfirmButton: false,
        timer: 2500
      })
      this.cdf.detectChanges()
      return
    }

    const data = {
      email: this.forgot_password_email,
      password: formData.password
    }
    this.dataService.post('update-password', data).then((result) => {
      if (result.success == true) {
        formData.password = null;
        formData.confirm_password = null;
        $('#uPassword').prop('hidden', true);
        $('#vEmail').prop('hidden', false);
        Swal.fire({
          position: 'center',
          icon: 'success',
          title: 'Success',
          showConfirmButton: false,
          timer: 2500
        });
        $('#forgot-password').modal('toggle');
      }
      else {
        Swal.fire({
          position: 'center',
          icon: 'error',
          text: 'Something went wrong. Please try again.',
          showConfirmButton: false,
          timer: 2500
        })
      }
      this.cdf.detectChanges()
    })
  }

  giveFeedback() {
    this.showFeedbackErrorAlert = false;
    this.showFeedbackSuccessAlert = false
    const data = {
      user_id: this.user_id,
      subject: $('#feedback_subject').val(),
      message: $('#feedback_message').val()
    };

    this.dataService.post('give-feedback', data).then((result) => {
      if (result.success == true) {
        $('#feedback_subject').val(null)
        $('#feedback_message').val(null)
        Swal.fire({
          position: 'center',
          icon: 'success',
          text: 'Feedback Received Successfully',
          showConfirmButton: false,
          timer: 2500
        })
      }
      else {
        Swal.fire({
          position: 'center',
          icon: 'error',
          text: result.message,
          showConfirmButton: false,
          timer: 2500
        })
      }
      this.cdf.detectChanges();
    })
  }

  cancelSubscription(id) {
    const data = {
      plan_id: id
    }

    this.dataService.post('host/cancel-subscription', data).then((result) => {
      this.getSubscription()
      this.cdf.detectChanges()
    })
  }

  buyPlan(id, level, amount) {
    this.plan_id = id
    this.level = level;
    this.showAlert = false;
    $('#hostplan1').modal('show')
  }

  showPaymentSection() {
    if ($('#stripe1').is(':checked')) {
      $('#stripeSection1').prop('hidden', false)
      $('#submitPlan1').prop('hidden', false)
      $('#paypalSection1').prop('hidden', true)
    }
    else {
      $('#stripeSection1').prop('hidden', true)
      $('#submitPlan1').prop('hidden', true)
      $('#paypalSection1').prop('hidden', false)
      const data = {
        plan_id: this.plan_id.toString()
      }
      this.dataService.post('guest/subscribe/paypal', data).then((result) => {
        if (result.link) {
          $('#clickPayPal1').prop('hidden', false)
          $('#paypalLink1').attr('href', result.link)
        }
        this.paypalInterval = setInterval(() => {
          this.verify();
        }, 5000);
      })
    }
    this.cdf.detectChanges()
  }

  verify() {
    this.dataService.get('guest/verify/paypal/success').then((result) => {
      if (result.success == true && result.data == 1) {
        $('#hostplan1').modal('hide')
        clearInterval(this.paypalInterval);
        this.updatePlan();
        this.getPaymentDetails();
        this.cdf.detectChanges();
      }
    })
  }

  updatePlan() {
    this.showAlert = false;
    this.showSubscribeAlert = false;
    this.errorMessage = null;
    this.successMessage = null;

    const data = {
      plan: this.level
    }

    this.dataService.post('host/update-plan-info', data).then((result) => {
      if (result.success = true) {
        localStorage.setItem('role', 'host');
        localStorage.setItem('data', JSON.stringify(result.data));
        localStorage.setItem('plan', result.data.plan);
        Swal.fire({
          position: 'center',
          icon: 'success',
          text: 'Package Subscribed Successfully.',
          showConfirmButton: false,
          timer: 2500
        })
        this.getSubscription()
        this.cdf.detectChanges()
      }
      else {
        Swal.fire({
          position: 'center',
          icon: 'error',
          text: 'Something went wrong. Please try again.',
          showConfirmButton: false,
          timer: 2500
        })
        this.cdf.detectChanges()
        return
      }
    },
      error => {
        Swal.fire({
          position: 'center',
          icon: 'error',
          text: 'Something went wrong. Please try again.',
          showConfirmButton: false,
          timer: 2500
        })
        this.cdf.detectChanges()
        return
      })
  }

  subscribePlan() {
    this.showAlert = false;
    this.errorMessage = null;
    this.cdf.detectChanges();

    const formData = this.planForm.getRawValue();

    const data = {
      plan_id: this.plan_id.toString(),
      type: 'card',
      number: formData.number,
      exp_month: formData.month,
      exp_year: formData.year,
      cvc: formData.cvc
    }

    if ($('#stripe1').is(':checked')) {
      this.dataService.post('guest/stripe_payment', data).then((result) => {
        if (result.success == 'Subscribe successfully.') {
          $('#hostplan1').modal('hide')

          this.planForm = this._formBuilder.group({
            plan_id: null,
            type: 'card',
            number: null,
            month: null,
            year: null,
            cvc: null
          })
          this.updatePlan();
          this.getPaymentDetails();
        }
        else {
          Swal.fire({
            position: 'center',
            icon: 'error',
            text: 'Something went wrong. Please try again.',
            showConfirmButton: false,
            timer: 2500
          })
          this.cdf.detectChanges()
          return
        }
      })
    }
  }

  deletePaymentMethod(payment_method) {
    const data = {
      payment_method: payment_method
    }
    this.dataService.post('host/delete-payment-details', data).then((result) => {
      this.getPaymentDetails()
    })
  }

  updatePayPal() {
    const data = {
      payment_method: 'paypal',
      email: $('#paypal-email1').val()
    }
    this.dataService.post('host/update-payment-details', data).then((result) => {
      this.getPaymentDetails()
    })
  }

  updateStripe() {
    const data = {
      payment_method: 'stripe',
      card: $('#stripe-card1').val(),
      exp_month: $('#stripe-month1').val(),
      exp_yearh: $('#stripe-year1').val(),
      cvc: $('#cvc1').val()
    }
    this.dataService.post('host/update-payment-details', data).then((result) => {
      this.getPaymentDetails()
    })
  }
  
}
