import { Component, OnInit, OnDestroy, ChangeDetectorRef, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, NgForm } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { data } from 'jquery';
import * as moment from 'moment';
import { ChatService } from 'src/app/_chat/chat.service';
import { DataService } from 'src/app/_services/data.services';
import Swal from 'sweetalert2';

@Component({
    selector: 'app-Bookdetails-cmp',
    templateUrl: './Bookdetails.component.html'
})

export class BookdetailsComponent implements OnInit, OnDestroy {
    user = null;
    details = null;
    sub = null;
    id = null;
    moment: any = moment;
    showAlert = false;
    errorMessage = null;
    offerMessages = null;
    user_id = null;
    ad_id = null;
    receiver_id = null;
    channel = null;
    publisher_id = null;
    sender_id=null;
    @ViewChild('bookingNgForm') bookingNgForm: NgForm;
    bookingForm: FormGroup;

    constructor(private router: Router,
        private routers: ActivatedRoute,
        private cdf: ChangeDetectorRef,
        private _formBuilder: FormBuilder,
        private dataService: DataService,
        private chat: ChatService) {
    }

    ngOnInit() {
        // if (localStorage.getItem("token") != null) {
        //     this.user = 'exists';
        // }
        // else {
        //     this.user = null;
        //     localStorage.clear();
        //     this.router.navigate(['/guest/home'])
        // }
        const body = document.getElementsByTagName('body')[0];
        body.classList.add('lock-page');
        body.classList.add('off-canvas-sidebar');
        const card = document.getElementsByClassName('card')[0];

        this.sub = this.routers.params.subscribe(params => {
            this.id = params['id'];
        });

        const data = {
            id: this.id
        }

        this.dataService.post('guest/get-booked-ad-by-id', data).then((result) => {
            this.details = result.data;
            if (this.details.ad.lat != null && this.details.ad.lat != '') {
                this.details.ad.lat = parseFloat(this.details.ad.lat)
            }
            if (this.details.ad.lan != null && this.details.ad.lan != '') {
                this.details.ad.lan = parseFloat(this.details.ad.lan)
            }
            let customer_id = this.details.customer.id
            this.publisher_id = this.details.ad.user_id
            this.receiver_id = this.details.ad.user_id
            this.sender_id = customer_id
            let ad_id = this.details.ad.id
            this.channel = customer_id.toString().concat(this.publisher_id.toString(), ad_id.toString());
            // this.channel = parseInt(this.channel);
            this.chat.joinChannel(this.channel);
            const chatData = {
                ad_id: ad_id
            }
            this.dataService.post('guest/get-guest-messages', chatData).then((result) => {
                if (!result.data.all_message) {}
                else {
                    console.log(result)
                    this.offerMessages = result.data.all_message
                   // this.receiver_id = result.data.ad.user_id;
                   // alert(this.receiver_id)
                    this.ad_id = result.data.ad_id;
                    this.user_id = localStorage.getItem('id')
                    setTimeout(function () { $('#divscroll').scrollTop(20000); }, 6000)
                }
            })
        })

        this.bookingForm = this._formBuilder.group({
            ad_id: this.id,
            message: null
        });

        this.chat.messages.subscribe(msg => {
            console.log(msg)
            if (msg.message !== null){
                if(msg.senderid==this.sender_id)
                {
                    $('#divscroll').append('<div class="col-12"><div class="mt-2 mr-5 pull-right" style="align-self: center; width: 270px; background-color:#e95228; height: auto; padding:10px; border-radius: 12px;"><p class="mb-1" style="color: white !important;">' + msg.message + '<p></p></div></div>')
                }
                else{
                $('#divscroll').append('<div class="col-12"><div class="mt-2 mr-5" style="align-self: center; width: 270px; background-color:#dcdcdc; height: auto; padding:10px; border-radius: 12px;"><p class="mb-1">' + msg.message + '<p><span class="float-right">just now</span></p></div></div>')

                }
            }
            else{
                if(msg.senderid==this.sender_id)
                {
                    $('#divscroll').append('<div class="col-12"><div class="mt-2 mr-5 pull-right" style="align-self: center; width: 270px; background-color:#e95228; height: auto; padding:10px; border-radius: 12px;"><p class="mb-1"><a href="http://oneroom3.crosip.com/' + msg.attachment_file  + '"target="_blank" style="color: white !important;">See Attachment</a><p></p></div></div>')
                }
                else{
                $('#divscroll').append('<div class="col-12"><div class="mt-2 mr-5" style="align-self: center; width: 270px; background-color: #dcdcdc; height: auto; padding:10px; border-radius: 12px;"><p class="mb-1"><a href="http://oneroom3.crosip.com/' + msg.attachment_file + '"target="_blank">See Attachment</a><p><span class="float-right">just now</span></p></div></div>')
                }
            }
            $('#divscroll').scrollTop(20000);
        })

    }

    ngOnDestroy() {
        this.chat.leaveChannel(this.channel);
        const body = document.getElementsByTagName('body')[0];
        body.classList.remove('lock-page');
        body.classList.remove('off-canvas-sidebar');
    }

    copyConfirmationNumber() {
        let tempVal = $('#confirmation_number').text();
        navigator.clipboard.writeText(tempVal).then().catch(e => console.error(e));
    }

    copyAddress() {
        let tempVal = $('#address').text();
        navigator.clipboard.writeText(tempVal).then().catch(e => console.error(e));
    }

    cancelBooking() {
        this.showAlert = false;
        if (this.bookingForm.invalid) {
            Swal.fire({
                position: 'center',
                icon: 'error',
                text: 'Please Fill All Required Fields',
                showConfirmButton: false,
                timer: 2500
            })
            this.cdf.detectChanges()
        }

        const formData = this.bookingForm.getRawValue();

        const data = {
            id: this.id,
            message: formData.message
        }

        this.dataService.post('guest/cancel-booked-offer', data).then((result) => {
            if (result.success == true) {
                Swal.fire({
                    position: 'center',
                    icon: 'success',
                    title: 'Success',
                    showConfirmButton: false,
                    timer: 2500
                })
                this.router.navigate(['guestportal/bookinglist']);
            }
        })
    }

    sendMessage(customer_id, publisher_id, ad_id) {

        if ($('#sender_message').val() == null || $('#sender_message').val() == '') {
            return;
        }

        let msg = $('#sender_message').val()
        let data1 = {
            receiver_id: this.receiver_id,
            ad_id: ad_id,
            message: msg,
            read_status: '0',
            attachment_file: null
        }
    console.log(data1)
        this.dataService.post('host/send-a-message', data1).then((result) => {
            if (result.success == true) {
                if (result.data.message != null) {
                    $('#divscroll').append('<div class="col-12"><div class="mt-2 mr-5 pull-right" style="align-self: center; width: 270px; background-color:#e95228; height: auto; padding:10px; border-radius: 12px;"><p class="mb-1" style="color: white !important;">' + result.data.message + '<p><span class="float-right" cstyle="color: white !important>' + moment(result.data.created_at).format('ddd') + ' | ' + moment(result.data.created_at).format('MMM DD, YYYY') + ' | ' + moment(result.data.created_at).format('hh:mm a') + '</span></p></div></div>')
                }
                $('#divscroll').scrollTop(20000);
                this.dataService.get('host/get-user-data').then((result1) => {
                    this.channel = customer_id.toString().concat(publisher_id.toString(), ad_id.toString());
                    this.channel = parseInt(this.channel);
                    let message = msg;
                    let date = moment().format('hh:mm a')
                    let data = {
                        message: message,
                        channel: this.channel,
                        date: date,
                        senderid: customer_id,
                        receiver_id: publisher_id,
                        pic: '',
                        attachment_file: '',
                        messageId: result.data.id,
                        senderNameFirstChar: result1.data.fname[0]
                    }
                    this.chat.sendMsg("channel", data);
                })
            }
            $('#sender_message').val(null);
            this.cdf.detectChanges();
        })
    }

    sendMessageAttachment(event: any, customer_id, publisher_id, ad_id) {
        const file: File = event.target.files[0];
        if (file) {
            const fd = new FormData();

            fd.append('receiver_id', publisher_id)
            fd.append('ad_id', this.ad_id)
            fd.append('read_status', '0')
            fd.append('attachment_file', file)

            this.dataService.post('host/send-a-message', fd).then((result) => {
                if (result.success == true) {
                    if (result.data.attachment_file != null) {
                        $('#divscroll').append('<div class="col-12"><div class="mt-2 mr-5 pull-right" style="align-self: center; width: 270px; background-color:#e95228; height: auto; padding:10px; border-radius: 12px;"><p class="mb-1"><a href="http://oneroom3.crosip.com/' + result.data.attachment_file + '"target="_blank" style="color: white !important;">See Attachment</a><p><span class="float-right" style="color: white !important;">' + moment(result.data.created_at).format('ddd') + ' | ' + moment(result.data.created_at).format('MMM DD, YYYY') + ' | ' + moment(result.data.created_at).format('hh:mm a') + '</span></p></div></div>')
                        $('#divscroll').scrollTop(20000);
                        this.dataService.get('host/get-user-data').then((result1) => {
                            let date = moment().format('hh:mm a')
                            let data = {
                                message: null,
                                channel: this.channel,
                                date: date,
                                senderid: customer_id,
                                receiver_id: publisher_id,
                                pic: result.data.attachment_file,
                                attachment_file: '',
                                messageId: result.data.id,
                                senderNameFirstChar: result1.data.fname[0]
                            }
                            this.chat.sendMsg("channel", data);
                        })
                    }
                }
                $('#sender_message').val(null);
                this.cdf.detectChanges();
            })
        }
    }
}
