import { Routes } from '@angular/router';

import { HomeComponent } from './Home/Home.component';
import { BookinglistComponent } from './Bookinglist/Bookinglist.component';
import { BookdetailsComponent } from './Bookdetails/Bookdetails.component';
import { OfferlistComponent } from './Offerlist/Offerlist.component';
import { OfferdetailsComponent } from './offerdetails/offerdetails.component';
import { DiscovernearComponent } from './Discovernear/Discovernear.component';
import { ProfileComponent } from './Profile/Profile.component';
import { BookingComponent } from './Booking/Booking.component';
import { SearchresultComponent } from './Searchresult/Searchresult.component';
import { DisputeDetailsComponent } from '../Guest/disputeDetails/disputedetails.component';

export const GuestportalRoutes: Routes = [
    {
        path: '',
        children: [ {
            path: 'bookinglist',
            component: BookinglistComponent
        }]
    },
    {
        path: '',
        children: [ {
            path: 'bookdetails/:id',
            component: BookdetailsComponent
        }]
    },
    {
        path: '',
        children: [ {
            path: 'disputedetails/:id',
            component: DisputeDetailsComponent
        }]
    },
    // {
    //     path: '',
    //     children: [ {
    //         path: 'offerlist',
    //         component: OfferlistComponent,
    //     }]
    // },
    {
        path: '',
        children: [ {
            path: 'profile',
            component: ProfileComponent
        }]
    }
    ,
    {
        path: '',
        children: [ {
            path: 'offerdetails/:id',
            component: OfferdetailsComponent
        }]
    },
    {
        path: '',
        children: [ {
            path: 'booking/:id',
            component: BookingComponent
        }]
    }
];
