import { Component, OnInit, OnDestroy, ChangeDetectorRef } from '@angular/core';
import { Router } from '@angular/router';
import * as moment from 'moment';
import { DataService } from 'src/app/_services/data.services';

@Component({
    selector: 'app-Bookinglist-cmp',
    templateUrl: './Bookinglist.component.html'
})

export class BookinglistComponent implements OnInit, OnDestroy {

    moment: any = moment;
    user = null;
    bookingList = null;
    bookingList0 = null;
    bookingList1 = null;
    bookingList2 = null;
    bookingList3 = null;

    constructor(private router: Router,
        private cdf: ChangeDetectorRef,
        private dataService: DataService) {
    }

    ngOnInit() {
        if (localStorage.getItem("token") != null) {
            this.user = 'exists';
        }
        else {
            this.user = null;
            localStorage.clear();
            this.router.navigate(['/guest/home'])
        }
        const body = document.getElementsByTagName('body')[0];
        body.classList.add('lock-page');
        body.classList.add('off-canvas-sidebar');
        const card = document.getElementsByClassName('card')[0];

        this.dataService.get('guest/get-all-booked-ads').then((result) => {
            this.bookingList = result.data;
            for(let i=0; i<this.bookingList.length; i++){
                this.bookingList0 = this.bookingList.filter(x => x.status == 0);
                this.bookingList1 = this.bookingList.filter(x => x.status == 1);
                this.bookingList2 = this.bookingList.filter(x => x.status == 2);
            }
        })

        this.dataService.get('guest/get-all-disputes').then((result) => {
            this.bookingList3 = result.data;
            for (let i = 0; i < this.bookingList3.length; i++) {
                let totalPrice = this.bookingList3[i]['offer']['price']
                let price = totalPrice * (this.bookingList3[i]['offer']['discount_of_percentage'] / 100)
                price = totalPrice - price;
                this.bookingList3[i]['offer']['current_price'] = price;
            }
        })

    }
    
    ngOnDestroy() {
        const body = document.getElementsByTagName('body')[0];
        body.classList.remove('lock-page');
        body.classList.remove('off-canvas-sidebar');
    }

    getBookingDetails(id) {
        this.router.navigate(['guestportal/bookdetails', id])
    }

    disputeDetails(id) {
        this.router.navigate(['guestportal/disputedetails', id])
    }
}
