import { Component, OnInit, OnDestroy, ChangeDetectorRef, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, NgForm } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import * as moment from 'moment';
import { DataService } from 'src/app/_services/data.services';
import { CountryISO, SearchCountryField } from "ngx-intl-tel-input";
import { AuthenticationService } from 'src/app/_services';
import Swal from 'sweetalert2';

@Component({
    selector: 'app-offerdetails-cmp',
    templateUrl: './offerdetails.component.html'
})

export class OfferdetailsComponent implements OnInit, OnDestroy {

    moment: any = moment;
    fullpath = null;
    user = null;
    details = null;
    sub = null;
    id = null;
    showAlert = false;
    errorMessage = null
    step = 0;
    isReadMore = true;

    @ViewChild('registerNgForm') registerNgForm: NgForm;
    registerForm: FormGroup;

    separateDialCode = false;
    SearchCountryField = SearchCountryField;
    CountryISO = CountryISO;
    preferredCountries: CountryISO[] = [
        CountryISO.UnitedStates,
        CountryISO.UnitedKingdom
    ];

    constructor(private router: Router,
        private _formBuilder: FormBuilder,
        private routers: ActivatedRoute,
        private cdf: ChangeDetectorRef,
        private authService: AuthenticationService,
        private dataService: DataService) { }

    ngOnInit() {
        if (localStorage.getItem("token")) {
            this.user = 'exists';
        }

        const body = document.getElementsByTagName('body')[0];
        body.classList.add('lock-page');
        body.classList.add('off-canvas-sidebar');
        const card = document.getElementsByClassName('card')[0];

        this.sub = this.routers.params.subscribe(params => {
            this.id = params['id'];
        });

        const data = {
            id: this.id
        }

        this.dataService.post('get-ad-by-id', data).then((result) => {
            this.details = result.data[0];
            this.fullpath = this.details.attachments[0].path;
            let totalPrice = this.details['price']
            let price = totalPrice * (this.details['discount_of_percentage'] / 100)
            this.details.discountPrice = price
            this.details.totalPrice = totalPrice - price;
            if (this.details.lat != null && this.details.lat != '') {
                this.details.lat = parseFloat(this.details.lat)
            }
            if (this.details.lan != null && this.details.lan != '') {
                this.details.lan = parseFloat(this.details.lan)
            }
        })

        this.registerForm = this._formBuilder.group({
            fname: null,
            lname: null,
            email: null,
            role: 'guest',
            contact_no: null,
            address: null,
            website: null,
            vat_no: null,
            pic: null
        });
        this.registerForm.controls['email'].disable();
    }

    ngOnDestroy() {
        const body = document.getElementsByTagName('body')[0];
        body.classList.remove('lock-page');
        body.classList.remove('off-canvas-sidebar');
    }

    bookNow() {
        let user_id = localStorage.getItem('id')
      //  alert(user_id)

        if (user_id == null) {
            $('.bd-example-modal-lg4').show()
        }
        else {
            this.dataService.get('host/get-user-data').then((result) => {
                this.registerForm = this._formBuilder.group({
                    fname: result.data.fname,
                    lname: result.data.lname,
                    email: result.data.email,
                    contact_no: result.data.country_calling_code + result.data.contact_no,
                    address: result.data.address,
                    website: result.data.website,
                    vat_no: result.data.vat_no,
                    pic: result.data.pic
                });
                this.registerForm.controls['email'].disable();
            })

            $('.bd-example-modal-lg4').show()
            this.cdf.detectChanges();
        }
        // this.router.navigate(['hostportal/editoffer', id])
    }

    sendOTP() {
        this.showAlert = false;
        const formData = this.registerForm.getRawValue();

        if (formData.email == null) {
            Swal.fire({
                position: 'center',
                icon: 'error',
                text: 'Please Fill All Required Fields',
                showConfirmButton: false,
                timer: 2500
            })
            this.cdf.detectChanges()
        }
        else {
            if (localStorage.getItem('id') != null) {
                this.step = 1;
            }
            else {
                const data = {
                    email: formData.email
                }
                this.dataService.post('test-me', data).then((result) => {
                    if (result.success == true) {
                        this.step = 1;
                    }
                    else {
                        Swal.fire({
                            position: 'center',
                            icon: 'error',
                            text: 'Soemthing went wrong. Please try again.',
                            showConfirmButton: false,
                            timer: 2500
                        })
                    }
                })
            }
            this.cdf.detectChanges()
        }
    }

    update() {
        if (this.registerForm.invalid) {
            Swal.fire({
                position: 'center',
                icon: 'error',
                text: 'Please Fill All Required Fields',
                showConfirmButton: false,
                timer: 2500
            })
            this.cdf.detectChanges()
            return
        }
        if (localStorage.getItem('id') == null) {
            this.step = 2;
            this.cdf.detectChanges()
        }
        else {
            this.showAlert = false;
            const formData = this.registerForm.getRawValue();
            const data = {
                fname: formData.fname,
                lname: formData.lname,
                contact_no: formData.contact_no.nationalNumber,
                country_calling_code: formData.contact_no.dialCode,
                country_code: formData.contact_no.countryCode,
                address: formData.address,
                website: formData.website,
                vat_no: formData.vat_no,
                pic: null
            };
            this.dataService.post('guest/update-personal-info', data).then((result) => {
                this.router.navigate(['guestportal/booking', this.id])
            },
                error => {
                    Swal.fire({
                        position: 'center',
                        icon: 'error',
                        text: 'Something went wrong. Please try again.',
                        showConfirmButton: false,
                        timer: 2500
                    })

                    this.cdf.detectChanges();
                }
            )
        }
    }

    register() {
        this.showAlert = false;

        let otp = $('#otp').val()
        if (otp == null || otp == '') {
            this.showAlert = true;
            this.errorMessage = 'Please Provide OTP Code';
            this.cdf.detectChanges()
            return
        }

        const formData = this.registerForm.getRawValue();

        const otpData = {
            email: formData.email,
            otp: otp
        }

        const data = {
            fname: formData.fname,
            lname: formData.lname,
            email: formData.email,
            password: formData.password,
            role: 'guest',
            contact_no: formData.contact_no.nationalNumber,
            country_calling_code: formData.contact_no.dialCode,
            country_code: formData.contact_no.countryCode,
            address: formData.address,
            website: formData.website,
            vat_no: formData.vat_no,
            pic: null
        };
        this.dataService.post('verify-register-otp', otpData).then((result) => {
            if (result.success == true) {
                this.authService.post('register', data).then((result) => {
                    if (result.success == true) {
                        localStorage.setItem('token', result.data.token);
                        localStorage.setItem('role', result.data.user.role);
                        localStorage.setItem('id', result.data.user.id);
                        this.router.navigate(['guestportal/booking', this.id])
                    }
                    else {
                        Swal.fire({
                            position: 'center',
                            icon: 'error',
                            text: 'Something went wrong. Please try again.',
                            showConfirmButton: false,
                            timer: 2500
                        })
                    }
                    this.cdf.detectChanges();
                },
                    error => {
                        Swal.fire({
                            position: 'center',
                            icon: 'error',
                            text: 'Soemthing went wrong. Please try again.',
                            showConfirmButton: false,
                            timer: 2500
                        })
                        this.cdf.detectChanges();
                    }
                )
            }
            else {
                Swal.fire({
                    position: 'center',
                    icon: 'error',
                    text: 'OTP code not valid',
                    showConfirmButton: false,
                    timer: 2500
                })
            }
            this.cdf.detectChanges()
        })
    }

    closeModal() {
        $('.bd-example-modal-lg4').hide()
    }

    showText() {
        this.isReadMore = !this.isReadMore
    }
}
