import { Component, OnInit, OnDestroy, AfterContentInit, ChangeDetectorRef, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, NgForm } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import * as moment from 'moment';
import { DataService } from 'src/app/_services/data.services';
declare var $: any;
import Swal from 'sweetalert2';
import { CountryISO, SearchCountryField } from "ngx-intl-tel-input";

@Component({
    selector: 'app-Booking-cmp',
    templateUrl: './Booking.component.html'
})

export class BookingComponent implements OnInit, AfterContentInit, OnDestroy {
    moment: any = moment;
    user = null;
    details = null;
    sub = null;
    id = null;
    showAlert = false;
    errorMessage = null;
    data = null;

    separateDialCode = false;
    SearchCountryField = SearchCountryField;
    CountryISO = CountryISO;
    preferredCountries: CountryISO[] = [
        CountryISO.UnitedStates,
        CountryISO.UnitedKingdom
    ];

    @ViewChild('bookingNgForm') bookingNgForm: NgForm;
    bookingForm: FormGroup;

    constructor(private router: Router,
        private _formBuilder: FormBuilder,
        private routers: ActivatedRoute,
        private cdf: ChangeDetectorRef,
        private dataService: DataService) {
    }

    ngOnInit() {
        if (localStorage.getItem("token") != null) {
            this.user = 'exists';
        }
        else {
            this.user = null;
            localStorage.clear();
            this.router.navigate(['/guest/home'])
        }
        const body = document.getElementsByTagName('body')[0];
        body.classList.add('lock-page');
        body.classList.add('off-canvas-sidebar');
        const card = document.getElementsByClassName('card')[0];

        this.sub = this.routers.params.subscribe(params => {
            this.id = params['id'];
        });

        const data = {
            id: this.id
        }
        this.dataService.post('get-ad-by-id', data).then((result) => {
            this.details = result.data[0];
            let totalPrice = this.details['price']
            let price = totalPrice * (this.details['discount_of_percentage'] / 100)
            this.details.discountPrice = price
            this.details.totalPrice = totalPrice - price;
            this.getUser()
        })


    }
    ngOnDestroy() {
        const body = document.getElementsByTagName('body')[0];
        body.classList.remove('lock-page');
        body.classList.remove('off-canvas-sidebar');
    }

    ngAfterContentInit() {
    }

    getUser() {
        this.dataService.get('host/get-user-data').then((result) => {
            this.data = result.data
            this.bookingForm = this._formBuilder.group({
                ad_id: this.details['id'],
                from: this.details['start_from'],
                to: this.details['end_to'],
                fname: this.data.fname,
                lname: this.data.lname,
                email: this.data.email,
                mobile_no: this.data.country_calling_code + this.data.contact_no,
                address: this.data.address,
                total_price: this.details['price'],
                discount_price: this.details['discountPrice'],
                net_price: this.details['totalPrice'],
                status: 0
            });
            this.bookingForm.controls['email'].disable();
            this.bookingForm.controls['fname'].disable();
            this.bookingForm.controls['lname'].disable();
            this.bookingForm.controls['mobile_no'].disable();
            this.bookingForm.controls['address'].disable();
            this.cdf.detectChanges()
        })
    }

    booking(){
        this.showAlert = false;
        const formData = this.bookingForm.getRawValue();

        const data = {
            ad_id: this.details['id'],
            from: this.details['start_from'],
            to: this.details['end_to'],
            fname: this.data.fname,
            lname: this.data.lname,
            email: this.data.email,
            mobile_no: formData.mobile_no.dialCode + formData.mobile_no.nationalNumber,
            address: this.data.address,
            total_price: this.details['price'],
            discount_price: this.details['discountPrice'],
            net_price: this.details['totalPrice'],
            status: 0
        };

        this.dataService.post('guest/book-ad', data).then((result) => {
            if(result.success == true){
                Swal.fire({
                    position: 'center',
                    icon: 'success',
                    title: 'Success',
                    showConfirmButton: false,
                    timer: 2500
                })
                this.router.navigate(['guestportal/bookinglist'])
            }
            else{
                Swal.fire({
                    position: 'center',
                    icon: 'error',
                    text: 'Something went wrong. Please try again.',
                    showConfirmButton: false,
                    timer: 2500
                  }) 
                this.cdf.detectChanges();
            }
        },
        error => {
            Swal.fire({
                position: 'center',
                icon: 'error',
                text: 'Something went wrong. Please try again.',
                showConfirmButton: false,
                timer: 2500
              }) 
            this.cdf.detectChanges();
        })
    }

    getOfferDetails(id) {
        this.router.navigate(['guestportal/offerdetails', id])
    }
}
