import { Component, OnInit, OnDestroy, ChangeDetectorRef } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import { Router } from '@angular/router';
import { DataService } from 'src/app/_services/data.services';

@Component({
  selector: 'app-Offerlist-cmp',
  templateUrl: './Offerlist.component.html'
})

export class OfferlistComponent implements OnInit, OnDestroy {
  test: Date = new Date();
  user = null;
  offers: any;
  step = 0;
  incval = 0;
  incval1 = 0;
  incval2 = 0;
  incval3 = 0;
  incval4 = 0;
  childList = []
  campaignOne: FormGroup;
  campaignTwo: FormGroup;

  constructor(private router: Router,
    private cdf: ChangeDetectorRef,
    private dataService: DataService,) {
    const today = new Date();
    const month = today.getMonth();
    const year = today.getFullYear();

    this.campaignOne = new FormGroup({
      start: new FormControl(new Date(year, month, 13)),
      end: new FormControl(new Date(year, month, 16))
    });

    this.campaignTwo = new FormGroup({
      start: new FormControl(new Date(year, month, 15)),
      end: new FormControl(new Date(year, month, 19))
    });
  }

  ngOnInit() {
    const body = document.getElementsByTagName('body')[0];
    body.classList.add('lock-page');
    body.classList.add('off-canvas-sidebar');
    const card = document.getElementsByClassName('card')[0];

    $(".bd-example-modal-lg").hide();
    $(".bd-example-modal-lg1").hide();

    if (localStorage.getItem("token")) {
      this.user = 'exists';
    }

    this.dataService.get('guest/get-all-ads').then((result) => {
      this.offers = result.data;
      for (let i = 0; i < this.offers.length; i++) {
        let totalPrice = this.offers[i]['price']
        let price = totalPrice * (this.offers[i]['discount_of_percentage'] / 100)
        price = totalPrice - price;
        this.offers[i]['current_price'] = price;
      }
    })

  }

  ngOnDestroy() {
    const body = document.getElementsByTagName('body')[0];
    body.classList.remove('lock-page');
    body.classList.remove('off-canvas-sidebar');
  }

  showdetails() {
    window.location.href = '#/guest/offerdetails';
  }

  hidecollaps(val) {
    if (val == 1) {
      $('#collapseExample1').removeClass("show");
    }
    else if (val == 2) {
      $('#collapseExample').removeClass("show");
    }
    else {
      $('#collapseExample').removeClass("show");
      $('#collapseExample1').removeClass("show");
    }
  }

  plus(val) {
    switch (val) {
      case 0:
        this.incval += 1;
        break;
      case 1:
        this.incval1 += 1;
        break;
      case 2:
        this.incval2 += 1;
        break;
      case 3:
        this.incval3 += 1;
        break;
      case 4:
        this.incval4 += 1;
        this.childList.push(this.incval4)
        this.cdf.detectChanges()
        break;
    }
  }

  minus(val) {
    switch (val) {
      case 0:
        this.incval -= 1;
        break;
      case 1:
        this.incval1 -= 1;
        break;
      case 2:
        this.incval2 -= 1;
        break;
      case 3:
        this.incval3 -= 1;
        break;
      case 4:
        this.incval4 -= 1;
        this.childList.pop()
        this.cdf.detectChanges()
        break;
    }
  }
}
